<template>
  <div id="app">

    <section class="container">
      <!-- only if item is in the active range -->
      <div :id="`js__item--${item.id}`" v-for="(item, index) in items"
      :key="item.key" :class="['item', {'active': item.isActive}]"
      v-if="activeItems.includes(index)"
      >
        <div class="item__content item__content--active" :id="`js__item__content--${item.id}`">
          <div class="item__content__top">
            <div class="item__content__top--flexy">
              <div class="item__content__high">
                <div class="item__content__button" @click="toggleBid(item)">Bid Now</div>
                <div style="display: flex;">
                  <svg class="item__content__high__icon" viewBox="0 0 50 50" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="1.414"><path d="M41.9 15.159H34.84a1.969 1.969 0 0 1 0-3.937h11.81c1.086 0 1.968.882 1.968 1.968V25a1.969 1.969 0 0 1-3.936 0v-7.058L29.343 33.28c-.767.77-2.015.77-2.782 0l-8.45-8.45-13.37 13.371c-.768.77-2.016.77-2.783 0a1.966 1.966 0 0 1 0-2.783L16.72 20.656a1.966 1.966 0 0 1 2.783 0l8.45 8.45 13.946-13.947z" fill="#00cea6" fill-rule="nonzero"/></svg>
                  <div class="item__content__high__number">${{item.currentBid}}</div>
                </div>
              </div>
            </div>
            <div class="item__content__high__bidder">#{{item.highBidder}}</div>
          </div>
          <div class="item__content__middle">
            <div class="item__content__title">{{item.name}}</div>
            <div class="item__content__description">{{item.description}}</div>
          </div>
        </div>

        <div class="item__content__bid" :id="`js__item__bid--${item.id}`">
          <div class="item__content__bid__title">{{item.name}} <span @click="closeItem(item)" style="color: #7934F7; cursor:pointer;">[x]</span></div>

          <div class="item__content__bid__box">
            <span class="item__content__bid__box__bid-label">Bidder Number</span>
            <input class="item__content__bid__box__bid-input" v-model="currentBidder" placeholder="00"/>
          </div>

          <div class="item__content__bid__box">
            <span class="item__content__bid__box__bid-label">Bid Amount</span>
            <input class="item__content__bid__box__bid-input" v-model="item.currentBid" :placeholder="item.currentBid"/>
          </div>

          <div class="item__content__bottom">
            <div class="item__content__button item__content__button--purple" @click="test(item)">Place Bid</div>
          </div>

        </div>
      </div>

    </section>


  </div>
</template>
<script>
import Firebase from 'firebase';

const app = Firebase.initializeApp({
  projectId: 'auction-e82cf',
  databaseURL: 'https://databaseName.firebaseio.com',
});

const db = app.firestore();
db.settings({ timestampsInSnapshots: true }); // needing this to keep error from happening

// some config before we mount vue
const numItemsPerPage = 3;

const initialActiveItems = [];
for (let i = 0; i < numItemsPerPage; i += 1) {
  initialActiveItems.push(i);
}

// / ACTUAL VUE STUFF
export default{
  data() {
    return {
      items: [],
      activeItems: initialActiveItems,
      leftOffAt: 0,
      numItemsPerPage: 3,
      currentBidder: '',
    };
  },
  mounted() {
    // every 30 seconds rotate the items
    // if query param, don't rotate and show all...
    if (!this.$route.query.rotate) {
      setInterval(() => {
        // if too big, reset
        if (this.leftOffAt > this.items.length) {
          this.leftOffAt = 0;
        }

        const newLeftOff = this.leftOffAt + this.numItemsPerPage;

        // push new numbers to array..could be more elegant but whatever
        const newRange = [];
        for (let i = this.leftOffAt; i < newLeftOff; i += 1) {
          newRange.push(i);
        }

        this.leftOffAt = newLeftOff;


        this.activeItems = newRange;
      }, 3000);
    }
  },
  // watch for firestore to fill before showing all
  watch: {
    items() {
      if (this.$route.query.rotate) {
        this.$nextTick(() => {
          const newRange = [];
          for (let i = 0; i < this.items.length; i += 1) {
            newRange.push(i);
          }
          console.log(newRange);
          this.activeItems = newRange;
        });
      }
    },
  },
  firestore: {
    // mapping local items to state for UI... maybe re
    items: db.collection('items').orderBy('order', 'asc'),
  },
  methods: {
    closeItem(item) {
      // OKAY SO HIDE THE DIV!
      document.querySelectorAll(`#js__item--${item.id} .item__content__bid--active`)[0].classList.remove('item__content__bid--active');
      document.querySelectorAll(`#js__item--${item.id} .item__content`)[0].classList.add('item__content--active');
    },
    test(item) {
      return db.collection('items').doc(item.id).update({
        currentBid: item.currentBid,
        highBidder: this.currentBidder,
        isActive: true,
      })
        .then(() => {
          console.log('Document successfully updated!');

          // OKAY SO HIDE THE DIV!
          document.querySelectorAll(`#js__item--${item.id} .item__content__bid--active`)[0].classList.remove('item__content__bid--active');
          document.querySelectorAll(`#js__item--${item.id} .item__content`)[0].classList.add('item__content--active');


          // EW, REFACTOR
          setTimeout(() => {
            db.collection('items').doc(item.id).update({
              isActive: false,
            }).then(() => {
              console.log('Active successfully removed');
            })
              .catch((err) => {
                console.log('error removing active class...', err);
              });
          }, 7000); // every 7 seconds
        })
        .catch((error) => {
          // The document probably doesn't exist.
          console.error('Error updating document: ', error);
        });
    },

    // / TOGGLE BID UI
    toggleBid(item) {
      document.querySelectorAll(`#js__item--${item.id} .item__content--active`)[0].classList.remove('item__content--active');
      document.querySelectorAll(`#js__item--${item.id} .item__content__bid`)[0].classList.add('item__content__bid--active');
    },

  },

};
</script>
